import React, {Component} from 'react';
import {Tree, Icon, Input, Tooltip, Button} from 'antd';
import {LoadingText} from 'mstr-react-library';

import '../style/ProjectChooser.css';

import backendHelper, {backend} from '../utilities/BackendHelper';
import Footer from './Footer';
import MainContainer from './MainContainer';
import StateContext, {modes} from '../StateContext';
import {compareByName, disableFocusOnMouseClick} from '../utilities/Utilities';
import UserTooltip from './UserTooltip';
import MSTRIcon from './MSTRIcon';

const {TreeNode} = Tree;
const icon = <MSTRIcon type="project-collapsed" />;

export default class ProjectChooser extends Component {
    constructor(props) {
        super(props);
        this.state = {
            allProjects: [],
            recentProjects: [],
            recentProjectsIds: [],
            selectedProjects: {
                all: [],
                recent: [],
            },
            loading: true,
            sortingType: -1,
            recentListExpanded: true,
        };
    }

    componentDidMount = async () => {
        const {envName, api} = this.context;
        const envInfo = this.context.recentProjects.find((e) => e.envName === envName);
        const recentProjectsIds = envInfo ? envInfo.projectList : [];
        const all = recentProjectsIds;
        const recent = recentProjectsIds;
        const projects = await api.getProjects();
        const allProjects = projects.sort(compareByName);
        const recentProjects = allProjects.filter((e) => recentProjectsIds.includes(e.id));
        this.setState({allProjects, recentProjects, recentProjectsIds, loading: false, selectedProjects: {all, recent}, projectsSaved: {all: allProjects, recent: recentProjects}},
            () => {this.parseToRealCheckboxes();});
        this.context.updateStateContext({selectedProjects: recent});
        document.addEventListener('keydown', this.handleKeyDown);
        disableFocusOnMouseClick('.project-chooser-header button, .ant-tree-checkbox-inner, button');
    }

    handleKeyDown = (e) => {
        if (e.keyCode === 13) {
            const prevented = [...document.querySelectorAll('.user-tooltip button, .links a, .project-chooser-header button')];
            if (!prevented.includes(e.target)) {
                document.querySelector('#next').click();
            }
        }
    }

    componentWillUnmount() {
        document.removeEventListener('keydown', this.handleKeyDown, false);
    }

    parseToRealCheckboxes = () => {
        const checkboxes = document.querySelectorAll('.ant-tree-checkbox-inner');
        checkboxes.forEach((e) => {
            e.setAttribute('role', 'checkbox');
            e.setAttribute('tabindex', 0);
            e.addEventListener('keydown', this.handleKeyOnCheckbox.bind(e));
        });
    }

    handleKeyOnCheckbox(event) {
        const {keyCode} = event;
        if (keyCode === 32) {
            this.click();
        }
    }

    handleCancel = () => {
        backendHelper.closeConnector();
    }

    handleNext = () => {
        backendHelper.updateRecentProjects(this.context.recentProjects);
        this.context.updateStateContext({mode: modes.BROWSING});
    }

    onCheck = (treeName, keys, event) => {
        const itemName = event.node.props.eventKey;
        let projectIds = itemName.indexOf('all') === 0 ? event.node.props.children.map((e) => e.key) : [itemName];
        const {checked} = event;
        let all = [...this.state.selectedProjects.all];
        let recent = [...this.state.selectedProjects.recent];

        if (checked) {
            projectIds.forEach((e) => {
                all.push(e);
                this.state.recentProjectsIds.includes(e) && recent.push(e);
            });
        }
        else {
            all = all.filter((e) => !projectIds.includes(e));
            recent = recent.filter((e) => !projectIds.includes(e));
        }
        this.updateProjectLists(recent, all);
    }

    onSearchChange = (e) => {
        const searchText = e.target.value.toLowerCase();
        const allProjects = this.state.projectsSaved.all.filter((e) => e.name.toLowerCase().includes(searchText));
        const recentProjects = this.state.projectsSaved.recent.filter((e) => e.name.toLowerCase().includes(searchText));
        this.setState({allProjects, recentProjects});
    }

    removeRecentProject = (id) => {
        const recentProjects = this.state.recentProjects.filter((e) => e.id !== id);
        const recent = this.state.selectedProjects.recent.filter((e) => e !== id);
        const all = this.state.selectedProjects.all.filter((e) => e !== id);
        const projectsSaved = {all: this.state.projectsSaved.all, recent: recentProjects};
        this.setState({recentProjects, projectsSaved});
        this.updateProjectLists(recent, all);
    }

    sortProjectList = (typeCode) => {
        const allProjects = [...this.state.allProjects];
        const recentProjects = [...this.state.recentProjects];
        let sorter = ((a, b) => (a.name > b.name) ? typeCode : -typeCode);
        allProjects.sort(sorter);
        recentProjects.sort(sorter);
        const sortingType = -typeCode;
        this.setState({allProjects, recentProjects, sortingType});
    }

    sortProjectIdsByProjectNames = (idList) => {
        return this.state.projectsSaved.all.map((e) => e.id).filter((e) => idList.includes(e));
    }

    updateProjectLists = (recentUnsorted, allUnsorted) => {
        const recent = this.sortProjectIdsByProjectNames(recentUnsorted);
        const all = this.sortProjectIdsByProjectNames(allUnsorted);
        const {envName} = this.context;
        const curEnvObj = this.context.recentProjects.find((e) => e.envName === envName);
        const curEnv = curEnvObj ? curEnvObj : {envName, projectList: []};
        curEnv.projectList = [...all];
        let recentEnvs = this.context.recentProjects.filter((e) => e.envName !== envName);
        recentEnvs = [...recentEnvs, curEnv];
        this.setState({selectedProjects: {all, recent}});
        this.context.updateStateContext({selectedProjects: all, recentProjects: recentEnvs});
    }

    onTreeExpand = (expandedKeys, {expanded, node}) => {
        if (node.props.eventKey === 'all-recent') {
            this.setState({recentListExpanded: expanded});
        }
    }

    onTreeNodeLoad = (loadedKeys, {event, node}) => {
    }

    renderTree = (list, name) => {
        const {t} = this.context;
        const treeNodes = list.map((e) => {
            return <TreeNode title={e.name} key={e.id} dataRef={e} selectable={false} onLoad={this.onTreeNodeLoad} icon={icon}></TreeNode>;
        });

        const removeButtons = list.map((e) => {
            const title = t('Remove `{{projectName}}`', {projectName: e.name});
            return (
                <li key={e.id}>
                    <Tooltip title={title} placement="left">
                        <Icon type="close" onClick={() => this.removeRecentProject(e.id)} />
                    </Tooltip>
                </li>
            );
        });

        if (this.state.loading) return <div className="center-wrapper application-list-loading"><LoadingText text={t('Application list is loading...')} /></div>;

        return (
            <div className="tree-container checkbox-container">
                <div className="tree-title">
                    {t(name.charAt(0).toUpperCase() + name.slice(1))}
                    <div className="tree-count">
                        {list.length}
                    </div>
                </div>
                <div className="tree-content">
                    {name === 'recent' && this.state.recentListExpanded &&
                        <ul className="remove-buttons">
                            {removeButtons}
                        </ul>
                    }
                    <Tree
                        checkable
                        defaultExpandAll
                        checkedKeys={this.state.selectedProjects[name]}
                        onCheck={(keys, event) => this.onCheck(name, keys, event)}
                        showIcon
                        onExpand={this.onTreeExpand}>
                        <TreeNode title={`(${t('All')})`} key={`all-${name}`} icon={icon}>
                            {treeNodes}
                        </TreeNode>
                    </Tree>
                </div>
            </div>
        );
    }

    render() {
        const {t} = this.context;
        const {sortingType, selectedProjects, loading} = this.state;

        const header = (
            <div className="project-chooser-header">
                <h2>{t('Select Applications')}</h2>
                <div>
                    <Tooltip title={sortingType === 1 ? t('Sort ascending') : t('Sort descending')} placement='bottom'>
                        <button onClick={() => this.sortProjectList(sortingType)}><Icon type={sortingType === 1 ? 'sort-ascending' : 'sort-descending'} /></button>
                    </Tooltip>
                    <Input.Search
                        placeholder={t('Search') + '...'}
                        onChange={this.onSearchChange}
                        className='project-chooser-search' />
                    {
                        backend === 'Jupyter' && <UserTooltip />
                    }
                </div>
            </div>);

        const okButtonDisabled = selectedProjects.all.length > 0;
        const okButtonTooltipText = !okButtonDisabled ? t('This button is currently disabled because you didn\'t select any applications') : null;
        return (
            <MainContainer>
                <div className="project-chooser main-content">
                    {!loading && header}
                    <div className="recent-projects">
                        {this.state.recentProjects.length > 0 && this.renderTree(this.state.recentProjects, 'recent')}
                    </div>
                    <div className="all-projects">
                        {this.renderTree(this.state.allProjects, 'all')}
                    </div>
                </div>
                <Footer
                    right={
                        <div>
                            <Tooltip placement="topRight" title={okButtonTooltipText} mouseEnterDelay={1} overlayClassName='info-tooltip'>
                                <Button id="next" type='primary' onClick={this.handleNext} disabled={!okButtonDisabled}>{t('OK')}</Button>
                            </Tooltip>
                        </div>}
                />
            </MainContainer>
        );
    }
}

ProjectChooser.contextType = StateContext;
