import React, {Component} from 'react';
import {Input} from 'antd';

import '../style/TopBar.scss';
import StateContext, {modes} from '../StateContext';
import {disableFocusOnMouseClick} from '../utilities/Utilities';

export default class TopBar extends Component {
    changeMode = (mode) => {
        this.context.updateStateContext({...this.context, mode});
    }

    componentDidMount() {
        disableFocusOnMouseClick('.top-nav li a');
    }

    render() {
        const {t, mode} = this.context;
        return (
            <div className="top-bar">
                <div className="top-nav">
                    <ul>
                        <li><button onClick={() => this.changeMode(modes.BROWSING)} className={mode === modes.BROWSING ? 'active' : 'inactive'} id="import-data-tab-button">{t('Import Data')}</button></li>
                        <li><button onClick={() => this.changeMode(modes.EXPORT_DF_SELECT)} className={mode.startsWith('export') ? 'active' : 'inactive'} id="export-data-tab-button">{t('Export Data')}</button></li>
                    </ul>
                </div>
                <div className="top-bar__right">
                    <div className="object-search">
                        <Input.Search
                            placeholder={t('Search') + '...'}
                            onChange={this.props.onSearch}
                            className='project-chooser-search'
                            value={this.props.searchText} />
                    </div>
                </div>
            </div>
        );
    }
}

TopBar.contextType = StateContext;
