import i18n from 'i18next';
import {initReactI18next} from 'react-i18next';
import moment from 'moment';
import daCommon from './locales/da-DK/common';
import deCommon from './locales/de-DE/common';
import defaultCommon from './locales/default/common';
import enCommon from './locales/en-US/common';
import esCommon from './locales/es-ES/common';
import frCommon from './locales/fr-FR/common';
import itCommon from './locales/it-IT/common';
import jaCommon from './locales/ja-JP/common';
import koCommon from './locales/ko-KR/common';
import nlCommon from './locales/nl-NL/common';
import plCommon from './locales/pl-PL/common';
import ptCommon from './locales/pt-BR/common';
import svCommon from './locales/sv-SE/common';
import zhCNCommon from './locales/zh-CN/common';
import zhTWCommon from './locales/zh-TW/common';

moment.locale('ko-KR', {
    meridiem: function(hours, minutes, isLowercase) {
        return hours < 12 ? '오전' : '오후';
    },
});
moment.locale('zh-CN', {
    meridiem: function(hour, minute, isLowercase) {
        if (hour < 9) {
            return '早上';
        } else if (hour < 11 && minute < 30) {
            return '上午';
        } else if (hour < 13 && minute < 30) {
            return '中午';
        } else if (hour < 18) {
            return '下午';
        } else {
            return '晚上';
        }
    },
});
moment.locale('zh-TW', {
    meridiem: function(hour, minute, isLowercase) {
        if (hour < 9) {
            return '早上';
        } else if (hour < 11 && minute < 30) {
            return '上午';
        } else if (hour < 13 && minute < 30) {
            return '中午';
        } else if (hour < 18) {
            return '下午';
        } else {
            return '晚上';
        }
    },
});

i18n
    .use(initReactI18next) // passes i18n down to react-i18next
    .init({
        resources: {
            'default': {
                common: defaultCommon,
            },
            'da': {
                common: daCommon,
            },
            'de': {
                common: deCommon,
            },
            'en': {
                common: defaultCommon,
            },
            'en-US': {
                common: enCommon,
            },
            'es': {
                common: esCommon,
            },
            'fr': {
                common: frCommon,
            },
            'it': {
                common: itCommon,
            },
            'ja': {
                common: jaCommon,
            },
            'ko': {
                common: koCommon,
            },
            'nl': {
                common: nlCommon,
            },
            'pl': {
                common: plCommon,
            },
            'pt': {
                common: ptCommon,
            },
            'sv': {
                common: svCommon,
            },
            'zh': {
                common: zhCNCommon, // simplified Chinese
            },
            'zh-HK': {
                common: zhTWCommon, // traditional Chinese
            },
            'zh-TW': {
                common: zhTWCommon,
            },
        },
        saveMissing: true,
        saveMissingTo: 'all',
        lng: 'en-US',
        fallbackLng: 'default',
        load: 'all',
        keySeparator: false, // we do not use keys in form messages.welcome

        interpolation: {
            escapeValue: false, // react already safes from xss
            format: function(value, format, lng) {
                if (value instanceof Date) return moment(value).format(format);
                return value;
            },
        },
        debug: process.env.NODE_ENV !== 'production',
    });

i18n.on('languageChanged', (lng) => moment.locale(lng));

export default i18n;
