import React, {Component} from 'react';
import {Form, Input, Popover, Icon, Tooltip} from 'antd';

import backendHelper from '../utilities/BackendHelper';
import {REGEX_ENV_NAME} from './Authentication'
import StateContext from '../StateContext';
import MSTRIcon from './MSTRIcon';

const FormItem = Form.Item;

const formItemLayout = {
    colon: false,
    required: false,
};

const ENV_NAME_MAX_LENGHT = 150;

export default class EnvNameInput extends Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedSuggestion: {},
            isValidEnvUrl: false,
            curPos: 0,
        };
    };

    clearAllEnvSuggestions = () => {
        const suggestedEnvironments = {all: [], temp: []}
        const recentProjects = [];
        const datasetProperties = [];
        this.context.updateStateContext({suggestedEnvironments, recentProjects, datasetProperties});
        backendHelper.updateEnvSuggestions(suggestedEnvironments.all)
        backendHelper.updateRecentProjects(recentProjects);
        backendHelper.updateDatasetProperties(datasetProperties)
    }

    envNameFieldKey = (event) => {
        const keyCode = event.keyCode;
        if (keyCode === 13) this.onEnvNameEnter()
        if (keyCode === 38) this.onArrowKey(-1); //up
        if (keyCode === 40) this.onArrowKey(+1); //down
    }

    fillFieldsWithSuggestion = (envName, url, loginMode, username) => {
        const envNameField = document.getElementById('envName');
        const passField = document.getElementById('password');
        envNameField.blur();
        const {setFieldsValue} = this.props.form;
        setFieldsValue({envName, url, loginMode, username});
        passField.focus();
    }

    onArrowKey = (incr) => {
        const length = this.context.suggestedEnvironments.temp.length;
        const {curPos} = this.state;
        const newPos = Math.min(Math.max(curPos + incr, 0), length);

        if (newPos !== curPos) {
            const selected = document.querySelector('.recent-environments ul li.selected')
            selected && selected.classList.remove('selected');

            let selectedSuggestion;
            if (newPos !== 0) {
                selectedSuggestion = this.context.suggestedEnvironments.temp[newPos - 1];
                const selector = `.recent-environments ul li:nth-child(${newPos})`
                const row = document.querySelector(selector);
                row.classList.add('selected')
            }
            else if (newPos === 0) {
                selectedSuggestion = {};
            }
            this.setState({selectedSuggestion, curPos: newPos})
        }
    }

    onEnvNameChange = (e) => {
        const envName = e.target.value;
        const isValidEnvUrl = REGEX_ENV_NAME.test(envName);
        this.setState({isValidEnvUrl, selectedSuggestion: {}});
        const {all} = this.context.suggestedEnvironments;
        let temp;
        if (envName) {
            temp = this.context.suggestedEnvironments.all.filter((e) => {
                return e.envName.toLowerCase().includes(envName.toLowerCase());
            });
        } else {
            temp = [...all];
        }
        this.context.updateStateContext({suggestedEnvironments: {all, temp}})
    }


    onEnvNameEnter = () => {
        if (this.state.selectedSuggestion.envName) {
            const {envName, url, loginMode, username} = this.state.selectedSuggestion;
            this.fillFieldsWithSuggestion(envName, url, loginMode, username);
        }
        else {
            document.querySelector("#url").focus();
        }
    }

    removeSingleSuggestion = (envName) => {
        const all = this.context.suggestedEnvironments.all.filter((e) => e.envName !== envName)
        const recentProjects = this.context.recentProjects.filter(e => e.envName !== envName)
        const datasetProperties = this.context.datasetProperties.filter(e => e.envName !== envName)
        const suggestedEnvironments = {all, temp: all};

        this.context.updateStateContext({suggestedEnvironments, recentProjects, datasetProperties})
        backendHelper.updateEnvSuggestions(suggestedEnvironments.all);
        backendHelper.updateRecentProjects(recentProjects);
        backendHelper.updateDatasetProperties(datasetProperties);

        const envNameField = document.getElementById('envName');
        envNameField.focus();
    }

    generateSuggestionsList = () => {
        return this.context.suggestedEnvironments.temp.map((e) => {
            const {envName, url, loginMode, username} = e;
            return (
                <li key={envName}>
                    <div onClick={() => this.fillFieldsWithSuggestion(envName, url, loginMode, username)} title={envName + '\n\n' + url}>
                        <h3>{envName}</h3>
                        <p>{url}</p>
                    </div>
                    <div>
                        <Icon type="close" onClick={() => this.removeSingleSuggestion(envName)} />
                    </div>
                </li>);
        });
    }

    generateSuggestionPopoverContent = () => {
        const {t} = this.context;
        return (
            <div>
                <ul>
                    {this.generateSuggestionsList()}
                </ul>
                <p className="clear-recent">
                    <button onClick={() => this.clearAllEnvSuggestions()}>{t('Clear all recent')}</button>
                </p>
            </div>
        );
    }

    render() {
        const {getFieldDecorator} = this.props.form;
        const {t} = this.context;
        const errorMsg = (<Tooltip placement="bottom" title={t('Environment name can contain only latin letters, numbers, spaces and underscores.')}>
                            <div>
                                <MSTRIcon type="red-info" />
                                <span>{t('Enter a valid environment name')}</span>
                            </div>
                        </Tooltip>);
        const validateTrigger = 'onBlur';
        return (
            <FormItem label={t('Environment Name') + '*'} {...formItemLayout} wrapperCol={{md: {span: 8}}}>
                <Popover content={this.generateSuggestionPopoverContent()}
                    title={t('Recent environments')}
                    trigger='focus'
                    placement='bottomLeft'
                    overlayClassName={this.context.suggestedEnvironments.temp.length > 0 ? 'recent-environments' : 'recent-environments--hidden'}>
                    {getFieldDecorator('envName', {
                        rules: [{
                            required: true,
                            message: errorMsg,
                            pattern: REGEX_ENV_NAME
                        }],
                        onChange: this.onEnvNameChange,
                        validateTrigger,
                    })(
                        <Input placeholder={t('A nickname for your environment')} onKeyUp={this.envNameFieldKey} maxLength={ENV_NAME_MAX_LENGHT} autoComplete="off" />
                    )}
                </Popover>
            </FormItem>
        );
    }
}

EnvNameInput.contextType = StateContext;
