import React, {Component} from 'react';

import Authentication from './Authentication';
import BackendManager from './BackendManager';
import DataPreparator from './DataPreparator';
import ErrorBoundary from './ErrorBoundary';
import ImportData from './ImportData';
import ProjectChooser from './ProjectChooser';
import StateContext, {modes} from '../StateContext';
import DirectorySelect from './DirectorySelect';
import DataframeSelect from './DataframeSelect';
import DataWrangler from './DataWrangler';
import Loading from './Loading';
import {disableFocusOnMouseClick, reactOnTab} from '../utilities/Utilities';
import {backend} from '../utilities/BackendHelper';

export default class Content extends Component {
    constructor(props) {
        super(props);
        this.auth = React.createRef();
        this.authToken = {};
        this.sessionRefresher = {};
    }

    componentDidMount() {
        require(`../style/${process.env.REACT_APP_MSTR_BACKEND}.css`);
        this.showLoadingPage();
        document.addEventListener('keydown', reactOnTab);
        disableFocusOnMouseClick('button');
    }

    componentWillUnmount() {
        document.removeEventListener('keydown', reactOnTab);
    }

    componentDidUpdate() {
        disableFocusOnMouseClick('button');
    }

    tokenChecker = () => {
        this.authToken = setInterval(() => {
            if (!this.context.api.isAuthenticated) {
                this.context.updateStateContext({mode: modes.AUTHENTICATION});
                clearInterval(this.sessionRefresher);
                clearInterval(this.authToken);
            }
        }, 100);
    }

    keepSessionAlive = () => {
        const {timeout, sendDummyRequest} = this.context.api;
        const offset = 20;
        const interval = (timeout - offset) * 1000;
        this.sessionRefresher = setInterval(() => {
            sendDummyRequest();
        }, interval);
    }

    showLoadingPage = () => {
        const loadingFlagEnabled = document.location.search.search('loading=true') !== -1;
        if (backend === 'Jupyter' && loadingFlagEnabled) {
            this.context.updateStateContext({mode: modes.LOADING});
        }
    }

    content = () => {
        switch (this.context.mode) {
            case modes.PROJECT_SELECTION:
                return <ProjectChooser />;
            case modes.BROWSING:
                return <ImportData />;
            case modes.FILTER_SELECTION:
                return <DataPreparator />;
            case modes.EXPORT_DF_SELECT:
                return <DataframeSelect />;
            case modes.EXPORT_DATA_WRANGLER:
                return <DataWrangler />;
            case modes.EXPORT_DIR_SELECT:
                return <DirectorySelect />;
            case modes.LOADING:
                return <Loading t={this.context.t} />;
            default:
                return <Authentication tokenChecker={this.tokenChecker} keepSessionAlive={this.keepSessionAlive} />;
        }
    }

    render() {
        return (
            <ErrorBoundary>
                {this.content()}
                <BackendManager ref={(backendManager) => {window.backendManager = backendManager;}} tokenChecker={this.tokenChecker} keepSessionAlive={this.keepSessionAlive} />
            </ErrorBoundary>
        );
    }
}

Content.contextType = StateContext;
