import React, {Component} from 'react';
import {Row, Col, Button, message, Modal, Tooltip} from 'antd';
import TopBar from './TopBar';
import MainContainer from './MainContainer';
import Footer from './Footer';
import StateContext, {modes} from '../StateContext';
import FolderTree from './FolderTree';
import backendHelper, {backend} from '../utilities/BackendHelper';
import FolderContent from './FolderContent';
import {disableFocusOnMouseClick, sleep} from '../utilities/Utilities';

const REGEX_DATASET_NAME = /^\w+[\w\s]*$/;
const REGEX_FOLDER_NAME = REGEX_DATASET_NAME;

export default class DirectorySelect extends Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedDirectory: {
                type: '',
                id: '',
                projectId: '',
            },
            saveAsName: '',
            saveAsNameValid: false,
            treeSelectedKeys: [],
            treeExpandedKeys: [],
            treeComponentKey: Date.now(),
            newFolderName: '',
            newFolderNameValid: false,
            newFolderModalVisible: false,
            searchText: '',
        };
    }

    componentDidMount() {
        disableFocusOnMouseClick('.folder-tree-container, bu');
    }

    componentDidUpdate() {
        disableFocusOnMouseClick('button');
    }


    handleBack = () => {
        this.context.updateStateContext({mode: modes.EXPORT_DF_SELECT});
    }

    handleSave = () => {
        const {id} = this.state.selectedDirectory;
        const {selectedDataframes, api, dataframeWrangle} = this.context;
        const {saveAsName} = this.state;
        backendHelper.exportDataframes(selectedDataframes, id, saveAsName, api, dataframeWrangle);
    }

    onSaveAsChange = (e) => {
        const saveAsName = e.target.value;
        const saveAsNameValid = REGEX_DATASET_NAME.test(saveAsName);
        this.setState({saveAsNameValid, saveAsName});
    }

    onObjectChosen = (objectId, projectId, subtype, name, projectName) => {
        this.setState({saveAsName: name}, () => this.onSaveAsChange({target: {value: name}}));
    }

    onContentDoubleClick = (name, id, projectId, type, treeKey) => {
        if (type === 'project') projectId = id;
        const treeSelectedKeys = [treeKey];
        this.onTreeExpand(null, {expanded: true, node: {props: {eventKey: treeSelectedKeys[0]}}});
        this.setState({selectedDirectory: {type, id, projectId}, treeSelectedKeys});
    }


    onTreeSelect = (selectedKeys, e) => {
        const {eventKey} = e.node.props;
        let treeExpandedKeys = [...this.state.treeExpandedKeys];
        if (!treeExpandedKeys.includes(eventKey)) {
            treeExpandedKeys = [...treeExpandedKeys, eventKey];
            this.setState({treeExpandedKeys});
        }

        const treeSelectedKeys = [e.node.props.eventKey];
        const pos = e.node.props.pos.split('-')[1];
        const projectId = this.context.selectedProjects[pos];
        const {id, type} = e.node.props.dataRef;
        const typeName = type ? 'folder' : 'project';
        this.onTreeExpand(null, {expanded: true, node: {props: {eventKey}}});
        this.setState({selectedDirectory: {type: typeName, id, projectId}, treeSelectedKeys});
    }

    onTreeExpand = (keys, event) => {
        const {expanded} = event;
        const {eventKey} = event.node.props;
        let treeExpandedKeys = [...this.state.treeExpandedKeys];
        if (expanded) {
            treeExpandedKeys = [...treeExpandedKeys, eventKey];
        }
        else {
            treeExpandedKeys = treeExpandedKeys.filter((e) => e !== eventKey);
        }
        this.setState({treeExpandedKeys});
    }

    onNewFolder = async () => {
        this.setState({newFolderModalVisible: true});
        await sleep(1);
        disableFocusOnMouseClick('button');
    }

    onModalOk = () => {
        const {newFolderName} = this.state;
        this.createNewFolder(newFolderName);
    }

    onModalCancel = () => {
        this.clearAndHideModal();
    }

    onNewFolderNameInputChange = (e) => {
        const newFolderName = e.target.value;
        const newFolderNameValid = REGEX_FOLDER_NAME.test(newFolderName);
        this.setState({newFolderName, newFolderNameValid}, () => disableFocusOnMouseClick('.ant-modal-footer button'));
    }

    clearAndHideModal = () => {
        this.setState({
            newFolderModalVisible: false,
            newFolderName: '',
        });
    }

    createNewFolder = async (name) => {
        const {id, projectId} = this.state.selectedDirectory;
        const {t, api} = this.context;
        const newFolderJob = await api.createFolder(name, id, projectId);
        if (newFolderJob.success) {
            this.clearAndHideModal();
            const msg = t('Folder created successfully.');
            message.success(msg);
            document.querySelector('.footer').focus();
            window.folderContent.loadFolder(true);
            this.setState({treeComponentKey: Date.now()});
        }
        else {
            let msg;
            if (newFolderJob.message === 'Cannot create object because the name is in use. Please use a different name.') {
                msg = t('Cannot create folder because the name is in use. Please use a different name.');
            }
            else {
                msg = t('Cannot create folder. It may be that you don\'t have permissions to create folders in this directory or the name you selected is already in use.');
            }
            message.error(msg, 6);
            document.querySelector('.right-panel').focus();
        }
    }

    onSearch = (e) => {
        const {value} = e.target;
        this.setState({searchText: value});
    }

    render() {
        const {t, importOrExportInProgress} = this.context;
        const {selectedDirectory, saveAsName, saveAsNameValid, newFolderModalVisible, newFolderName, newFolderNameValid} = this.state;

        const isRStudio = backend === 'RStudio';
        const disableSave = selectedDirectory.id === '' || selectedDirectory.type !== 'folder' || !saveAsNameValid || importOrExportInProgress;
        const saveButtonTooltipTitle = disableSave
            ? importOrExportInProgress
                ? isRStudio
                    ? null
                    : t('This button is disabled because another dataset is being currently fetched or exported.')
                : t('This button is currently disabled because you didn\'t choose the output directory or the name you specified is invalid')
            : null;
        const saveAsInputClassName = (!saveAsName) ? 'valid' : saveAsNameValid ? 'valid' : 'invalid';
        const folderNameInputClassName = (!newFolderName) ? 'valid' : newFolderNameValid ? 'valid' : 'invalid';
        const saveAsInputTooltipVisible = (saveAsName.length > 0 && !saveAsNameValid);
        const folderNameInputTooltipVisible = newFolderName && !newFolderNameValid;
        const newFolderModalOkButtonDisabled = !(newFolderName && newFolderNameValid);

        return (
            <MainContainer>
                <Row className="full-height directory-selection">
                    <TopBar
                        onSearch={this.onSearch}
                        searchText={this.state.searchText} />
                    <Row className="saveas-container">
                        <Tooltip visible={saveAsInputTooltipVisible} placement="bottom" title={t('Dataset name can contain only latin letters, numbers, spaces and underscores.')}>
                            <label htmlFor="saveas">Save As</label>
                            <input type="text" name="saveas" id="saveas" value={this.state.saveAsName} onChange={this.onSaveAsChange} className={saveAsInputClassName} maxLength="100" autoFocus />
                        </Tooltip>
                    </Row>
                    <Row className="browser-content directory-selection-content">
                        <Col className="full-height left-panel" tabIndex="-1">
                            <FolderTree
                                api={this.context.api}
                                onTreeSelect={this.onTreeSelect}
                                hideDatasets={true}
                                selectedProjects={this.context.selectedProjects}
                                selectedKeys={this.state.treeSelectedKeys}
                                onTreeExpand={this.onTreeExpand}
                                expandedKeys={this.state.treeExpandedKeys}
                                key={this.state.treeComponentKey} />
                        </Col>
                        <Col className="full-height right-panel">
                            <FolderContent
                                mstrApi={this.context.api}
                                onDoubleClick={this.onContentDoubleClick}
                                onObjectChosen={this.onObjectChosen}
                                selectedProjects={this.context.selectedProjects}
                                selectedDirectory={this.state.selectedDirectory}
                                selectableCubes
                                searchText={this.state.searchText}
                                ref={(folderContent) => {window.folderContent = folderContent;}} />
                        </Col>
                    </Row>
                </Row>
                <Footer
                    left={<div>
                        <Button id="back" onClick={this.handleBack}>{t('Back')}</Button>
                        <Button id="newFolder" onClick={this.onNewFolder}>{t('New folder')}</Button>
                        <Modal
                            title={t('Create new folder')}
                            visible={newFolderModalVisible}
                            onOk={this.onModalOk}
                            onCancel={this.onModalCancel}
                            okButtonProps={{disabled: newFolderModalOkButtonDisabled}}>
                            <p>{t('Specify new folder name')}</p>
                            <Tooltip visible={folderNameInputTooltipVisible} placement="bottom" title={t('Folder name can contain only latin letters, numbers, spaces and underscores.')}>
                                <input type="text" name="new-folder-name-input" id="new-folder-name-input" value={this.state.newFolderName} onChange={this.onNewFolderNameInputChange} className={folderNameInputClassName} maxLength="100" autoFocus autoComplete="off" />
                            </Tooltip>
                        </Modal>
                    </div>}
                    right={<div>
                        <Tooltip placement="topRight" title={saveButtonTooltipTitle} mouseEnterDelay={1} overlayClassName={disableSave ? 'info-tooltip' : 'hidden'}>
                            <Button id="save" onClick={this.handleSave} type="primary" disabled={disableSave}>{t('Save')}</Button>
                        </Tooltip>
                    </div>}>
                </Footer>
            </MainContainer>
        );
    }
}

DirectorySelect.contextType = StateContext;
