import React, {Component} from 'react';
import {Popover, Tooltip} from 'antd';
import StateContext, {modes} from '../StateContext';
import backendHelper, {version, backend, helpUrl} from '../utilities/BackendHelper';
import {sleep, disableFocusOnMouseClick} from '../utilities/Utilities';

import i18n from '../i18n';
import '../style/UserTooltip.scss';

const CONTACT_US_MAIL = 'info@microstrategy.com';
const lb = '%0D%0A'; // line break for mail

export default class UserTooltip extends Component {
    state = {
        visible: false,
    }

    handleManageApplications = () => {
        this.context.updateStateContext({mode: modes.PROJECT_SELECTION});
    }

    handleLogout = () => {
        this.context.api.logout();
        this.context.updateStateContext({reloadDataSource: true});
    }

    onOpenTootip = async () => {
        backendHelper.onUserTooltipOpen();
        this.setState({visible: true});
        await sleep(1);
        disableFocusOnMouseClick('.user-tooltip-overlay a, .user-tooltip-overlay button');
        this.registerClicksOusideTooltip();
        this.manageKeysInTooltip();
    }

    registerClicksOusideTooltip = () => {
        const userTooltip = document.querySelector('.user-tooltip-overlay');
        const outsideClickListener = (event) => {
            if (!userTooltip.contains(event.target)) {
                this.setState({visible: false});
                removeClickListener();
            }
        };
        const removeClickListener = () => {
            document.removeEventListener('click', outsideClickListener);
        };
        document.addEventListener('click', outsideClickListener);
    }

    manageKeysInTooltip = () => {
        const userTooltip = document.querySelector('.user-tooltip-overlay');
        const linksContainer = userTooltip.querySelector('.links');
        linksContainer.focus();
        const links = [...linksContainer.querySelectorAll('a, button')];
        const firstLink = links[0];
        const lastLink = links[links.length - 1];

        const trapTabKey = (e) => {
            const {keyCode, shiftKey} = e;
            if (keyCode === 9) {
                if (shiftKey && document.activeElement === firstLink) {
                    e.preventDefault();
                    lastLink.focus();
                }
                else if (!shiftKey && document.activeElement === lastLink) {
                    e.preventDefault();
                    firstLink.focus();
                }
            }

            if (keyCode === 27) {
                this.setState({visible: false});
                document.querySelector('.logo').focus();
            }
        };

        linksContainer.addEventListener('keydown', trapTabKey);
    }

    mailTemplate = () => {
        const {packageVersion} = this.context;
        const {t, backendParameters} = this.context;
        let template = `${lb}${lb}${lb}${lb}----- ${t('Please don’t change the text below. Type your message above this line.')} -----
        ${lb}Connector type: ${backend}
        ${lb}Connector version: ${packageVersion}`;
        Object.keys(backendParameters).forEach((key) => {
            template += `${lb}${key}: ${backendParameters[key]}`;
        });
        template += `${lb}UserAgent: ${navigator.userAgent}`;
        return template;
    }

    render() {
        const {t, api, packageVersion} = this.context;
        const {envUrl, userInfo} = api;
        const {fullName, initials} = userInfo;
        const mailSubject = `[${i18n.language}] MicroStrategy for ${backend} feedback`;

        const tooltipContent = (<div className="tooltip-content-container">
            <div className="user-info">
                <div className="circle"><span className="initials">{initials}</span></div>
                <div className="name-url">
                    <h3>{fullName}</h3>
                    <p>{envUrl}</p>
                </div>
            </div>
            <div className="links" tabIndex="-1">
                <ul>
                    <li><button onClick={() => this.handleManageApplications()}>{t('Manage Applications')}</button></li>
                    <li><a href={helpUrl} target="_blank" rel="noopener noreferrer">{t('Help')}</a></li>
                    <li><a href={`mailto:${CONTACT_US_MAIL}?subject=${mailSubject}&body=${this.mailTemplate()}`}>{t('Contact Us')}</a></li>
                    <li><button onClick={() => this.handleLogout()}>{t('Log out')}</button></li>
                </ul>
                <div className="version-number">{t('Version {versionNumber}', {versionNumber: packageVersion})}</div>
            </div>
        </div>);

        return (
            <div className="user-tooltip">
                <Popover content={tooltipContent} placement="leftTop" trigger="click" overlayClassName="user-tooltip-overlay" visible={this.state.visible}>
                    <Tooltip title={t('More items')} placement="bottomRight" mouseEnterDelay={1}>
                        <button onClick={() => this.onOpenTootip()}>&#8942;</button>
                    </Tooltip>
                </Popover>
            </div>
        );
    }
}

UserTooltip.contextType = StateContext;
