import React, {Component} from 'react';

import {backend} from '../utilities/BackendHelper'
import Header from './Header'
import StateContext from '../StateContext';

export default class MainContainer extends Component {
    render() {
        const {t, api, mode} = this.context;
        const title = t(`MicroStrategy for {{programName}}`, {programName: backend});
        return (
            <div className="content" mode={mode}>
                <Header title={title} visible={api.isAuthenticated} t={t} />
                {this.props.children}
            </div>
        );
    }
}

MainContainer.contextType = StateContext;
