import React, {Component} from 'react';
import {withTranslation} from 'react-i18next';

import backendHelper, {defaultEnvName, backend} from './utilities/BackendHelper';
import Content from './components/Content';
import MSTRFetch from './utilities/MSTRFetch';
import StateContext, {modes} from './StateContext';

const packageJson = require('../package.json');
const {version} = packageJson;

class App extends Component {
  constructor(props) {
    super(props);
    this.api = new MSTRFetch();
    this.updateStateContext = (newState) => {
      if (newState.mode) backendHelper.notifyOnModeChange(newState.mode);
      this.setState(newState);
    };

    this.state = {
      mode: modes.AUTHENTICATION,
      selectedProjects: [],
      suggestedEnvironments: {
        all: [],
        temp: [],
      },
      recentProjects: [],
      envName: null,
      api: this.api,
      updateStateContext: this.updateStateContext,
      datasetProperties: [],
      t: (text) => text,
      backendEnvName: defaultEnvName,
      dataFrames: [],
      dataframeContent: {},
      dataframeWrangle: {},
      selectedDataframes: [],
      reloadDataSource: true,
      backendParameters: [],
      browsingSearchText: '',
      savedRecord: {},
      importOrExportInProgress: false,
      selectedDataset: {},
      packageVersion: version,
    };
  }

  componentDidMount() {
    const {t} = this.props;
    this.api.translateFunction = t;
    const title = t(`MicroStrategy for {{programName}}`, {programName: backend});
    document.title = title;
    this.setState({api: this.api, t});
  }

  render() {
    return (
      <StateContext.Provider value={this.state}>
        <Content />
      </StateContext.Provider>
    );
  }
}

export default withTranslation('common')(App);
