/* eslint-disable camelcase */
import {supportedTypes, createBody} from './MSTRFetch';

export default class JupyterHelper {
  constructor() {
    this.connectorIsLoaded();
  }

  connectorIsLoaded = () => {
    let message_type = 'conn_load';
    window.top.postMessage({message_type}, '*');
  }

  sendDatasetInfo = (api, dataset) => {
    // const {username, password, loginMode, envUrl} = api;
    const envUrl = api.envUrl;
    const {projectId, projectName, objectId, subtype, name, parameters} = dataset;
    let datasetType = supportedTypes.reportTypes.includes(subtype) ? 'report' : 'cube';

    let body;
    if (parameters.length > 0) {
      const {attributes, metrics, filters} = parameters;
      body = JSON.stringify(createBody(attributes, metrics, filters));
    } else {
      body = 'null';
    }

    const connectionData = {
      body: body,
      name: name,
      datasetType: datasetType,
      datasetId: objectId,
      url: envUrl,
      projectId: projectId,
      projectName: projectName,
    };

    // username, password, loginMode
    const authenticationInfo = api.credentials;
    let message_type = 'import';
    window.top.postMessage({message_type, connectionData, authenticationInfo}, '*');
  };

  closeConnector = () => {
    let message_type = 'cancel';
    window.top.postMessage({message_type}, '*');
  };

  updateEnvSuggestions = (newEnvInfos) => {
    let message_type = 'update_envs';
    let value = JSON.stringify(newEnvInfos);
    window.top.postMessage({message_type, value}, '*');
  };

  updateRecentProjects = (newRecentProjects) => {
    let message_type = 'update_proj';
    let value = JSON.stringify(newRecentProjects);
    window.top.postMessage({message_type, value}, '*');
  };

  updateDatasetProperties = (properties) => {
    let message_type = 'update_properties';
    let value = JSON.stringify(properties);
    window.top.postMessage({message_type, value}, '*');
  }

  updateDataframeColumnName = (dataframe_name, prev_col_name, new_col_name) => {
    let message_type = 'wrangle_col_name';
    let instructionDetails = {
      df_name: dataframe_name,
      prev: prev_col_name,
      next: new_col_name,
    };
    instructionDetails = JSON.stringify(instructionDetails);
    window.top.postMessage({message_type, instructionDetails}, '*');
  }

  reorderColumns = (dataframe_name, columns_in_row, start_index) => {
    let message_type = 'wrangle_col_reorder';
    let instructionDetails = {
      df: dataframe_name,
      cols: columns_in_row,
      start: start_index,
    };
    instructionDetails = JSON.stringify(instructionDetails);
    window.top.postMessage({message_type, instructionDetails}, '*');
  }

  sendMessageToConsole = (message) => {};

  notifyOnModeChange = (mode) => {
    const message_type = 'update_mode';
    window.top.postMessage({message_type, value: mode}, '*');
  }

  exportDataframes = (names, folderId, saveAsName, api, wrangle) => {
    const exportInfo = {
      dataframe: names,
      wrangle: wrangle,
      url: api.envUrl,
      folder_id: folderId,
      save_as_name: saveAsName,
      folderId: folderId,
      projectName: api.projectName,
      projectId: api.project,
    };

    // username, password, loginMode
    const authenticationInfo = api.credentials;

    let message_type = 'export_dataframe';
    window.top.postMessage({message_type, exportInfo, authenticationInfo}, '*');
  }

  onExportDataMode = () => {
    let message_type = 'export_mode';
    window.top.postMessage({message_type}, '*');
  }

  onExportGatherDetails = (dataframe_name) => {
    let message_type = 'gather_df_details';
    window.top.postMessage({message_type, dataframe_name}, '*');
  }

  onUserTooltipOpen = () => {
    const message_type = 'tooltip';
    window.top.postMessage({message_type}, '*');
  }

  onLogin = (token, credentials) => {
    const message_type = 'on_login';
    window.top.postMessage({message_type, token, credentials}, '*');
  }
}
