import React, {Component} from 'react';
import FolderItem from './FolderItem';
import lineClamp from 'line-clamp';

import {defineType} from '../utilities/MSTRFetch';
import '../style/FolderContent.scss';
import StateContext from '../StateContext';

export default class FolderContent extends Component {
    constructor(props) {
        super(props)

        this.state = {
            items: [],
            lastFetched: {
                id: '',
                type: '',
            },
            selectedItem: {
                name: '',
                id: '',
                projectId: '',
                subtype: '',
            }
        }
    }

    componentDidMount = async () => {
        await this.loadRootFolder();
        this.clampLines();
    }

    componentDidUpdate = async () => {
        if (this.props.selectedDirectory.id !== "") {
            await this.loadFolder();
            this.clampLines();
        }
    }

    clampLines = () => {
        const maxNumberOfLines = 2;
        document.querySelectorAll('.folder-item .name').forEach(e => {
            lineClamp(e, maxNumberOfLines + 1);
        })
    }


    loadFolder = async (force = false) => {
        const {id, type, projectId} = this.props.selectedDirectory;
        const {lastFetched} = this.state
        const notFetchedOneStepAgo = (id !== lastFetched.id || type !== lastFetched.type);
        if (notFetchedOneStepAgo || force) {
            const isMyPersonal = type === "folder" && id === projectId && id !== "";
            const isProject = type === "project";
            const folderId = isProject ? "" : id;

            const {mstrApi} = this.props;
            mstrApi.projectId = projectId;
            let folderContent = isMyPersonal ? await mstrApi.getMyPersonalObjects() : await mstrApi.getFolderContent(folderId);
            // const parentDir = await this.getParentDirectory({folderId, isMyPersonal, isProject});
            folderContent = folderContent.filter(e => !e.hidden);
            if (isProject) {
                const additionalFolder = {name: "My Personal Objects", id: projectId, type: 8}
                folderContent = [...folderContent, additionalFolder];
            };
            const items = this.parseContent(folderContent, projectId, null, folderId);
            this.setState({items, lastFetched: {id, type}})
        }
    }

    getParentDirectory = async ({folderId, isMyPersonal, isProject}) => {
        const {mstrApi} = this.props;
        let type;
        let id;
        if (!isMyPersonal && !isProject) {
            const folderInfo = await mstrApi.getFolderInfo(folderId);
            const {ancestors} = folderInfo;
            if (ancestors.length === 1) {
                type = 'project';
                id = mstrApi.projectId;
            }
            else if (ancestors.length > 1) {
                type = 'folder';
                id = ancestors[ancestors.length - 1].id;
            }
        }
        else if (isMyPersonal && !isProject) {
            type = 'project';
            id = mstrApi.projectId;
        }
        else {
            type = 'all-projects';
            id = null;
        }
        return {type, id}
    }


    loadRootFolder = async () => {
        const {selectedProjects, mstrApi} = this.props;
        let projectTree = await mstrApi.getProjects();
        if (projectTree) {
            projectTree = selectedProjects ? projectTree.filter(e => selectedProjects.includes(e.id)) : projectTree;
        } else {
            throw new Error('No project tree returned by getProjects!');
        }
        const items = this.parseContent(projectTree, "", 'project');
        this.setState({items})
    }


    parseContent = (content, projectId, predefinedType, folderId) => {
        const {token} = this.context.api;

        return content.map(e => {
            let type = e.type === 8 ? "folder" : defineType(e.subtype);
            type = predefinedType || type;
            let key;
            if (type === "project") {
                key = `${token}${e.id}PROJ`
            }
            else if (type === "folder") {
                key = `${token}${e.id}${folderId}${projectId}`
            }
            if (e.name === "My Personal Objects") {
                key = `${token}PO${e.id}`
            }
            return {
                name: e.name,
                id: e.id,
                typeName: predefinedType || type,
                type: e.type,
                subtype: e.subtype,
                projectId,
                treeKey: key,
            }
        })
    }

    onItemClick = (name, id, projectId, subtype) => {
        this.setState({selectedItem: {name, id, projectId, subtype}})
        this.props.onObjectChosen(id, projectId, subtype, name);
    }

    render() {
        const {selectableProjects = false, selectableFolders = false, selectableCubes = false, selectableReports = false, searchText} = this.props
        const {t} = this.context;
        let items = [...this.state.items];
        items = items.filter(e => e.name.toLowerCase().includes(searchText.toLowerCase()));
        const projects = items.filter(e => e.typeName === "project").map(e => <FolderItem {...e} key={e.id} selectable={selectableProjects} openable onDoubleClick={this.props.onDoubleClick} />)
        const folders = items.filter(e => e.typeName === "folder").map(e => <FolderItem {...e} key={e.id} selectable={selectableFolders} openable onDoubleClick={this.props.onDoubleClick} />)
        const cubes = items.filter(e => e.typeName === "cube").map(e => <FolderItem {...e} onClick={this.onItemClick} key={e.id} selectable={selectableCubes} selected={e.id === this.state.selectedItem.id} />)
        const reports = items.filter(e => e.typeName === "report").map(e => <FolderItem {...e} onClick={this.onItemClick} key={e.id} selectable={selectableReports} selected={e.id === this.state.selectedItem.id} />)

        return (
            <div className="tree-view-folder-content">
                {items.length === 0 && searchText === '' &&
                    <div className="no-data">
                        <h3>
                            {t('Folder is empty.')}
                        </h3>
                    </div>
                }
                {items.length === 0 && searchText !== '' &&
                    <div className="no-data">
                        <h3>
                            {t('No results for specified search query.')}
                        </h3>
                    </div>
                }
                {projects.length > 0 &&
                    <div className="section">
                        <h3>{t('Projects')}</h3>
                        {projects}
                    </div>}
                {folders.length > 0 &&
                    <div className="section">
                        <h3>{t('Folders')}</h3>
                        {folders}
                    </div>}
                {cubes.length > 0 &&
                    <div className="section">
                        <h3>{t('Datasets')}</h3>
                        {cubes}
                    </div>}
                {reports.length > 0 &&
                    <div className="section">
                        <h3>{t('Reports')}</h3>
                        {reports}
                    </div>}
            </div>
        )
    }
}

FolderContent.contextType = StateContext;