import RStudioHelper from './RStudioHelper';
import JupyterHelper from './JupyterHelper';

const backend = process.env.REACT_APP_MSTR_BACKEND;
let applicationType;
let defaultEnvName;
let backendHelper;
let helpUrl;

switch (backend) {
    case 'RStudio':
        backendHelper = new RStudioHelper();
        helpUrl = 'https://www2.microstrategy.com/producthelp/current/MSTR-for-RStudio/Content/mstr_for_rstudio.htm';
        defaultEnvName = 'Global Environment';
        applicationType = 65;
        break;
    case 'Jupyter':
        backendHelper = new JupyterHelper();
        helpUrl = 'https://www2.microstrategy.com/producthelp/current/MSTR-for-Jupyter/Content/mstr_for_jupyter.htm';
        defaultEnvName = 'Untitled1';
        applicationType = 64;
        break;
    default:
        backendHelper = new RStudioHelper();
}

export {backend, defaultEnvName, helpUrl, applicationType};
export default backendHelper;
