/* eslint max-lines-per-function:0*/
import React, {Component} from 'react';
import {Table, Popover} from 'antd';
import {MSTRIcon} from 'mstr-react-library';
import MSTRIcon2 from '../../MSTRIcon';
import './object-browser.css';
import {objectTypes} from 'mstr-react-library';
import {LoadingText} from 'mstr-react-library';
import DatasetInfomodal from './dataset-infomodal';
import StateContext from '../../../StateContext';

import {disableFocusOnMouseClick, sleep} from '../../../utilities/Utilities'

const renderNameIcon = ({type, subtype}) => {
    const iconType = objectTypes.getTypeDescription(type, subtype) ? objectTypes.getTypeDescription(type, subtype).toLowerCase() : null;
    return (
        <span>
            <MSTRIcon type={iconType} />
        </span>
    );
};

export const sortGlobal = (a, b) => {
    a = typeof a === "string" ? a.toLowerCase() : a;
    b = typeof b === "string" ? b.toLowerCase() : b;
    return a < b ? -1 : a > b ? 1 : 0;
};

const renderCertIcon = ({isCertified}) => {
    return (
        <span>
            {isCertified && <MSTRIcon type='certified' />}
        </span>
    );
};

const PAGE_SIZE = 25;

export class ObjectBrowser extends Component {
    static defaultProps = {
        t: (text, opts = {}) => opts.date ? opts.date.toString() : text,
        onSorterChange: () => {},
        onScrollUpdated: () => {},
        dataSource: [],
        onSizeUpdated: () => {},
        sorter: {
            columnKey: 'dateModified',
            order: 'descend',
        }
    };

    constructor(props) {
        super(props);
        const selectedRow = props.selectedItem || {};

        this.state = {
            selectedRowKeys: selectedRow.objectId ?
                [selectedRow.objectId + selectedRow.projectId] : [],
            selectedRows: selectedRow.objectId ? [selectedRow] : [],
            pageSize: props.pageSize || PAGE_SIZE,
            scrollPosition: null,
            modalProps: {
                contextMenuVisible: false,
                x: 0,
                y: 0,
                datasetId: '',
                projectId: ''
            }
        };
    };

    COLUMN_DEFINITIONS = [
        {
            render: renderNameIcon,
            width: 20,
            className: 'icon-column',
        },
        {
            title: 'Name',
            key: 'name',
            sorter: (a, b) => sortGlobal(a.name, b.name),
            // fix for width prop not working for long words https://github.com/ant-design/ant-design/issues/5753
            render: ({name}) => {
                return (
                    <Popover placement="bottomLeft" content={name} mouseEnterDelay={1} overlayClassName="dataset-name-tooltip">
                        <div style={{'maxWidth': this.props.nameCellMaxWidth}}>{name}</div>
                    </Popover>
                );
            },
            className: 'name-column',
        },
        {
            render: renderCertIcon,
            width: 20,
            className: 'certification-column',
        },
        {
            title: 'Owner',
            key: 'owner',
            sorter: (a, b) => sortGlobal(a.owner, b.owner),
            render: ({owner}) => {
                return (
                    <Popover placement="bottomLeft" content={owner} mouseEnterDelay={1}>
                        <div style={{'maxWidth': '150px'}}>{owner}</div>
                    </Popover>
                );
            },
            width: 160,
            className: 'owner-column',
        },
        {
            title: 'Application',
            key: 'projectId',
            sorter: (a, b) => sortGlobal(a.applicationName, b.applicationName),
            render: ({applicationName}) => {
                return (
                    <Popover placement="bottomLeft" content={applicationName} mouseEnterDelay={1}>
                        <div style={{'maxWidth': '150px'}} title={applicationName}>{applicationName}</div>
                    </Popover>
                );
            },
            width: 180,
            className: 'application-column',
        },
        {
            title: 'Modified',
            key: 'dateModified',
            sorter: (a, b) => a.dateModified - b.dateModified,
            render: ({dateModified}) => {
                try {
                    // return (<div style={{'maxWidth': '180px'}}>{this.props.t('refreshed_date', {date: dateModified})}</div>);
                    return (<div style={{'maxWidth': 'unset'}}>{this.props.t('refreshed_date', {date: dateModified})}</div>);
                } catch (e) {
                    return (<div style={{'maxWidth': '180px'}}>{dateModified.toLocaleString()}</div>);
                }
            },
            width: 180,
            className: 'modified-column',
        },
    ];

    componentDidMount() {
        if (this.context.savedRecord.key) {
            this.selectRow(this.context.savedRecord);
        }
    }

    // TODO: temporary solution.
    // Once we support multichoice on rows, we need to rethink and change it
    componentDidUpdate(prevProps, prevState) {
        if (this.state.selectedRows[0]
            && this.state.selectedRowKeys[0] !== prevState.selectedRowKeys[0]) {
            this.props.onObjectChosen(this.state.selectedRows[0].objectId,
                this.state.selectedRows[0].projectId,
                this.state.selectedRows[0].subtype,
                this.state.selectedRows[0].name,
                this.state.selectedRows[0].projectName);
        }
        if (prevProps.dataSource.length !== this.props.dataSource.length && this.context.selectedDataset.objectId) {
            const selectedDatasetId = this.context.selectedDataset.objectId;
            const selectedIsOnVisibleList = this.props.dataSource.some(({key}) => key === selectedDatasetId);
            if (!selectedIsOnVisibleList) {
                this.context.updateStateContext({selectedDataset: {}, savedRecord: {}});
                this.setState({selectedRowKeys: [], selectedRows: []});
            }
        }
    }

    // TODO: temporary solution.
    // Once we support multichoice on rows, we need to rethink and change it
    selectRow = (record) => {
        const selectedRow = {
            objectId: record.key,
            projectId: record.projectId,
            subtype: record.subtype,
            name: record.name,
            projectName: record.applicationName,
        };
        if (this.state.selectedRowKeys.includes(selectedRow.objectId)) {
            return;
        }
        this.setState({
            selectedRowKeys: [selectedRow.objectId + selectedRow.projectId],
            selectedRows: [selectedRow],
        });
        this.context.updateStateContext({savedRecord: record})
    }

    onSelectedRowKeysChange = (selectedRowKeys, records) => {
        this.selectRow(records[0]);
    }

    createTableRef = (node) => {
        if (node) {
            const DOMNode = document.querySelector('.object-browser .ant-table-body');
            if (DOMNode) {
                if (this.props.scrollPosition) {
                    DOMNode.scrollTop = this.props.scrollPosition;
                }
                DOMNode.addEventListener('scroll', () => {
                    this.setState({scrollPosition: DOMNode.scrollTop});
                    this.props.onScrollUpdated(DOMNode.scrollTop);
                    if (DOMNode.scrollHeight - (DOMNode.scrollTop + 25) <= DOMNode.clientHeight) {
                        this.setState({pageSize: this.state.pageSize + PAGE_SIZE}, () =>
                            this.props.onSizeUpdated(this.state.pageSize));
                    }
                });
            }
        }
    };

    onAntdTableChange = (pagination, filters, sorter) => {
        const {columnKey, order} = sorter;
        if (this.props.sorter.columnKey !== columnKey || this.props.sorter.order !== order) {
            this.props.onSorterChange({columnKey, order});
        }
    };

    onRowContextMenu = async (event, {key, projectId}) => {
        event.preventDefault();
        if (!this.state.modalProps.contextMenuVisible) {
            const that = this;
            document.addEventListener(`click`, function onClickOutside() {
                that.setState({modalProps: {...that.state.modalProps, contextMenuVisible: false}})
                document.removeEventListener(`click`, onClickOutside)
            })
        }
        this.setState({
            modalProps: {
                contextMenuVisible: true,
                x: event.clientX,
                y: event.clientY,
                datasetId: key,
                projectId
            }
        });
        await sleep(1);
        disableFocusOnMouseClick('button');
    }

    render() {
        const {t} = this.props;
        const {selectedRowKeys, modalProps} = this.state;
        const rowSelection = {
            selectedRowKeys,
            type: 'radio', // TODO: to be removed after multichoice added
            onChange: this.onSelectedRowKeysChange,
        };
        const tableClassName = this.props.dataSource[0] ? 'table-container object-browser' : 'smart-folders-empty-table object-browser'; // TODO: don't remove the header at no data
        const newColumnDefinitions = this.COLUMN_DEFINITIONS.map((column) => {
            if (column.key === this.props.sorter.columnKey) {
                column.defaultSortOrder = this.props.sorter.order;
            } else {
                delete column.defaultSortOrder;
            }
            return column;
        });
        const emptyText = <div className="no-data-placeholder">
            <MSTRIcon2 type="empty-folder-no-results" />
            <p>{t('None of the objects matched your search.')}</p>
            <p>{t('Try another search.')}</p>
        </div>
        return (
            <div>
                <Table className={tableClassName}
                    ref={this.createTableRef}
                    loading={{indicator: <LoadingText text={t('Loading Data')} />, spinning: this.props.loading}}
                    rowSelection={rowSelection}
                    rowKey={(item) => item.key + item.projectId}
                    locale={{sortTitle: '', emptyText}}
                    dataSource={this.props.dataSource}
                    columns={newColumnDefinitions.map((item) => ({...item, title: t(item.title)}))}
                    pagination={{pageSize: this.state.pageSize}}
                    scroll={{x: 963, y: true}}
                    onChange={(...args) => this.onAntdTableChange(...args)}
                    onRow={(record) => ({
                        onClick: () => {
                            this.selectRow(record);
                        },
                        onContextMenu: event => this.onRowContextMenu(event, record)
                    })}
                />
                <DatasetInfomodal {...modalProps} />
            </div>
        );
    }
}

ObjectBrowser.contextType = StateContext;